<template>
    <div class="comments">
        <div class="comment-list">
            <el-card v-for="comment in commentList" :key="comment.id">
                <div class="user-info">
                    <el-avatar :src="comment.author.avatar" :size="40"></el-avatar>
                    <span class="username">{{ comment.author.username }}</span>
                </div>

                <div class="comment-content">{{ comment.content }}</div>

                <div class="comment-actions">
                    <el-button type="text">回复</el-button>
                    <el-button type="text" @click="toggleReplyList(comment)">
                        {{ comment.showReplyList ? '收起回复' : '查看回复' }}
                    </el-button>
                </div>

                <div class="reply-list" v-show="comment.showReplyList">
                    <el-card v-for="reply in comment.childComments" :key="reply.id" class="sub-comment">
                        <div class="user-info">
                            <el-avatar :src="reply.author.avatar" :size="30"></el-avatar>
                            <span class="username">{{ reply.author.username }}</span>
                        </div>
                        <div class="comment-content" style="margin-top: 8px">{{ reply.content }}</div>
                        <div class="comment-actions" style="margin-left: 20px">
                            <el-button type="text">回复</el-button>
                        </div>
                    </el-card>
                </div>
            </el-card>
        </div>
        <el-pagination
            :current-page.sync="pageNum"
            :page-size="pageSize"
            v-if="total !== 0"
            layout="prev, pager, next"
            :total="total"
            @current-change="change"
        >
        </el-pagination>
        <div class="add-comment">
            <el-input type="textarea" v-model="commentInput" placeholder="请输入评论内容"></el-input>
            <el-button type="primary">发表评论</el-button>
        </div>
    </div>
</template>

<script>
export default {
    name:'Comment',
    props: ['comments', 'commentTotal', 'page', 'size'],
    data() {
        return {
            commentList: [],
            total: 0,
            pageNum: 0,
            pageSize: 0,
            activeNames: [],
            commentInput: ''
        };
    },

    watch: {
        comments(value) {
            this.commentList = value
        },

        commentTotal(value) {
            this.total = value
            console.log(this.total)
        }
    },

    mounted() {
        this.commentList = this.comments
        this.total = this.commentTotal
        this.pageNum = this.page
        this.pageSize = this.size
    },

    methods: {
        toggleReplyList(comment) {
            comment.showReplyList = !comment.showReplyList
        },

        change() {
            // console.log(this.pageNum)
            this.$emit('getPage', this.pageNum)
        }
    }
};
</script>

<style scoped>
.comments {
    margin: 20px;
}

.comment-list {
    margin-top: 20px;
}

.comment-list .el-card {
    margin-top: 20px;
}

.user-info {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.username {
    margin-left: 10px;
    font-weight: bold;
}

.comment-content {
    margin-top: 10px;
    float: left;
    text-align: justify;
}

.comment-actions {
    float: right;
}

.sub-comment .username {
    margin-left: 5px;
}

.sub-comment .comment-content {
    margin-top: 5px;
}

.add-comment {
    margin-top: 20px;
}

.add-comment .el-input {
    height: 50px;
}

.add-comment .el-button {
    margin-top: 20px;
    float: right;
}

.reply-list {
    margin-top: 60px;
    margin-left: 10px;
}

.sub-comment {
    margin-top: 10px;
    display: flex;
    align-items: center;
}

.block {
    margin-top: 10px;
    float: right;
}
</style>
