<template>
    <div>
        <el-carousel :interval="4000" type="card" height="500px">
            <el-carousel-item v-for="carouse in carousels" :key="carouse.id">
                <el-image :src="carouse.url" style="width: 100%; height: 500px"></el-image>
            </el-carousel-item>
        </el-carousel>
    </div>
</template>

<script>
import axios from "axios"

export default {
    name: 'Carousel',
    data() {
        return {
            carousels: []
        }
    },
    created() {
        this.getCarousels()
    },
    methods: {
        getCarousels() {
            axios({
                method: "GET",
                url: "http://localhost:8989/slideshow/queryAllEnable"
            }).then(response => {
                this.carousels = response.data.data
            }).catch(error => {
                this.$message.error("网站出现问题，请联系管理员")
            })
        }
    }
}
</script>

<style scoped>
.el-carousel {
    margin: 20px;
}

.el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
}
</style>
