<template>
    <div>
        <el-divider content-position="center">咨询</el-divider>
        <el-tabs v-model="activeName" type="card">
            <el-tab-pane
                v-for="category in categories"
                :key="category.id"
                :label="category.name"
                :name="category.name"
                :lazy="true">
                <Children :cates="category.cates"/>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
import axios from "axios"
import Children from "@/components/category/children.vue"

export default {
    name: 'Category',
    components: {Children},
    data() {
        return {
            categories: [],
            activeName: ''
        }
    },
    created() {
        this.getCategories()
    },
    methods: {
        getCategories() {
            axios({
                method: 'GET',
                url: 'http://localhost:8989/auth/category/queryAllParent'
            }).then(response => {
                if (response.data.code === 200) {
                    this.categories = response.data.data
                    this.activeName = this.categories[0].name
                }
            }).catch(error => {
                this.$message.error("网站出现问题，请联系管理员")
            })
        }
    }
}
</script>

