<template>
    <div class="article-list">
        <el-card class="box-card" v-for="article in articles" :key="article.id">
            <el-descriptions class="margin-top" :title="`文章标题：${article.title}`" :column="3" border>
                <template slot="extra">
                    <el-button type="primary" size="small" @click="showDetail(article.id)">查看文章详情内容</el-button>
                </template>
                <el-descriptions-item>
                    <template slot="label">
                        <i class="el-icon-view"></i>
                        浏览量
                    </template>
                    <el-tag>{{ article.readNum }}</el-tag>
                </el-descriptions-item>
                <el-descriptions-item>
                    <template slot="label">
                        <i class="el-icon-star-off"></i>
                        点赞量
                    </template>
                    <el-tag type="danger">{{ article.likeNum }}</el-tag>
                </el-descriptions-item>
                <el-descriptions-item>
                    <template slot="label">
                        <i class="el-icon-circle-close"></i>
                        不喜欢
                    </template>
                    <el-tag type="info">{{ article.dislikeNum }}</el-tag>
                </el-descriptions-item>
                <el-descriptions-item>
                    <template slot="label">
                        <i class="el-icon-user"></i>
                        发布用户
                    </template>
                    {{ article.userId }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template slot="label">
                        <i class="el-icon-date"></i>
                        发布时间
                    </template>
                    {{ article.publishTime }}
                </el-descriptions-item>
            </el-descriptions>
        </el-card>

        <el-pagination
            background
            layout="prev, pager, next"
            :total="total"
            :page-size="searchObj.pageSize"
            :current-page.sync="searchObj.pageNum"
            @current-change="getArticleList"
            v-if="total !== 0"
        >
        </el-pagination>

        <el-empty v-if="total === 0" description="暂无咨询"></el-empty>

        <el-drawer
            :title="article.title"
            :visible.sync="drawer"
            :direction="direction"
            :destroy-on-close="true"
            size="50%"
        >
            <div style="text-align: center; font-size: 15px; margin-bottom: 30px; color: rgb(128,128,128)">作者：{{username}}</div>
            <span v-html="article.content"></span>

            <Comment @getPage="getPage" :size="pageSize" :comments="commentList" :commentTotal="commentTotal" :page="pageNum"/>
        </el-drawer>
    </div>
</template>

<script>
import Comment from "@/components/comment/index.vue"
import axios from "axios"

export default {
    name: 'Article',
    props: ['categoryId'],
    components: {Comment},
    data() {
        return {
            pageNum: 1,
            pageSize: 1,
            articles: [],
            commentList:[],
            article: {},
            username: '',
            total: 0,
            drawer: false,
            direction: 'rtl',
            articleId: 0,
            searchObj: {
                pageNum: 1,
                pageSize: 5,
                status: '审核通过'
            },
            commentTotal: 0
        }
    },
    created() {
        this.searchObj.categoryId = this.categoryId
        this.getArticleList();
    },
    methods: {
        getArticleList() {
            axios({
                method: "POST",
                url: "http://localhost:8989/articles/query",
                data: this.searchObj
            }).then(response => {
                if (response.data.code === 200) {
                    this.articles = response.data.data.records
                    this.total = response.data.data.total
                }
            }).catch(error => {
                this.$message.error("网站出现问题，请联系管理员")
            })
        },

        showDetail(id) {
            this.articleId = id
            axios({
                method: "GET",
                url: "http://localhost:8989/articles/" + id
            }).then(response => {
                if (response.data.code === 200) {
                    this.article = response.data.data
                    this.username = this.article.author.username
                    this.getComments()
                    this.drawer = true
                }else {
                    this.$message.error(response.data.msg)
                }
            }).catch(error => {
                this.$message.error("网站出现问题，请联系管理员")
            })
        },

        changeData(data) {
            for (let i = 0; i < data.length; i++) {
                data[i].showReplyList = false
            }
            return data
        },

        getPage(page) {
            this.pageNum = page
            this.getComments()
        },

        getComments() {
            axios({
                method: "GET",
                url: `http://localhost:8989/auth/comment/queryByArticleId/${this.articleId}`,
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize
                }
            }).then(response => {
                if (response.data.code === 200) {
                    this.commentList = this.changeData(response.data.data.records || [])
                    this.commentTotal = response.data.data.total
                    console.log("article组件加载完毕，拿到数据", this.commentTotal)
                }else {
                    this.$message.error(response.data.msg)
                }
            })
        }
    }
}
</script>

<style scoped>
.box-card {
    width: 1200px;
    margin: 0 auto;
}

.el-card {
    margin-bottom: 30px;
}
</style>
